import React, { useEffect, useState } from 'react'
import './covid.css';
const Covid = () => {

    const [data, setdata] = useState([]);

    const getCovidData = async () => {
        try {
            const res = await fetch('https://corona.lmao.ninja/v2/countries/pakistan');
            const data = await res.json();
            console.log(data.cases);
            setdata(data);

        } catch (err) {
            console.error(err);
        }
    }
    useEffect(() => {
        getCovidData();

    }, []);
    return (
        <>

            {/* <h1>ðŸ”´ LIVE</h1> */}
            <h2>COVID-19 Tracker</h2>
            <ul>
                <li className="card AA">
                    <div className="card_main AA">
                        <div className="card_inner cont ">
                            {/* <p className="card_name"><span>OUR </span> Country</p> */}
                            <p className="card_total card_small">{data.country}</p>
                            {/* <img src={data.countryInfo.flag}></img> */}
                        </div>
                    </div>
                </li>
                <li className="card">
                    <div className="card_main">
                        <div className="card_inner">
                            <p className="card_name"><span>Total</span>Cases</p>
                            <p className="card_total card_small">{data.cases}</p>

                        </div>
                    </div>
                </li>
                <li className="card">
                    <div className="card_main">
                        <div className="card_inner">
                            <p className="card_name"><span>Today</span>Cases</p>
                            <p className="card_total card_small">{data.todayCases}</p>
                        </div>
                    </div>
                </li>
                <li className="card">
                    <div className="card_main">
                        <div className="card_inner">
                            <p className="card_name"><span>Total</span>Deaths</p>
                            <p className="card_total card_small">{data.deaths}</p>
                        </div>
                    </div>
                </li>
                <li className="card">
                    <div className="card_main">
                        <div className="card_inner">
                            <p className="card_name"><span>Today</span>Deaths</p>
                            <p className="card_total card_small">{data.todayDeaths}</p>
                        </div>
                    </div>
                </li>
                <li className="card">
                    <div className="card_main">
                        <div className="card_inner">
                            <p className="card_name"><span>Today</span>Recovered</p>
                            <p className="card_total card_small">{data.todayRecovered}</p>
                        </div>
                    </div>
                </li>
            </ul>
        </>
    )
}

export default Covid
